document.addEventListener('DOMContentLoaded', () => {

    hoverEnlace()

    navegacionFija()

    desplegarMenu()

    // portadaResponsive()

});

//cambiar de elemento en el menu de escritorio
function hoverEnlace(){
    const cambioEtiqueta = document.querySelectorAll('.cambio');
    const elementos = cambioEtiqueta;

    elementos.forEach(elemento => {
        elemento.addEventListener('mouseover', function(){
            elemento.classList.toggle('mostrar');
            elemento.classList.toggle('ocultar');
        });
        elemento.addEventListener('mouseout', function(){
            elemento.classList.toggle('mostrar');
            elemento.classList.toggle('ocultar');
        })
    });
};

function navegacionFija(){

    const barra = document.querySelector('.logotipo');

    //registrar la interseccion del elemento
    const observer  = new IntersectionObserver(entries => {

        if(entries[0].isIntersecting){
            barra.classList.remove('fijo')
        }else{
            barra.classList.add('fijo')
        }
    })

    //elemento obsevar
    observer.observe(document.querySelector('.barra'));
}

//menu boton
function desplegarMenu(){
    const menuBoton = document.querySelector('.menu');
    let menuOpen = false;
    const navegacion = document.querySelector('#nav')
    

    menuBoton.addEventListener('click',() => {
        if(!menuOpen){
            menuBoton.classList.add('open');
            navegacion.classList.add('mostrar-nav');
                
            menuOpen = true;
        }else{
            menuBoton.classList.remove('open');
            navegacion.classList.remove('mostrar-nav');
                
            menuOpen = false;
        }
        
        
});

}

